<template>
  <div class="mt-7 grid grid-cols-3 gap-5">
    <div class="col-span-2 space-y-5">
      <div class="card p-4 lg:px-5">
        <div
          class="flex justify-between w-full items-center bg-slate-100 p-3 rounded font-bold"
        >
          <span>{{ $t("titles.workspaces") }}</span>

          <router-link
            :to="{ name: 'app.workspace.create' }"
            class="text-white btn bg-primary font-medium hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
          >
            {{ $t("models.workspace.add") }}
            <i class="fa fa-plus fa-sm fa-fw"></i>
          </router-link>
        </div>
      </div>

      <workspace-item
        v-for="workspace in workspaces"
        :key="workspace.id"
        :workspace="workspace"
      ></workspace-item>
    </div>
    <div class="col-span-1">
      <calendar
        :attributes="surveyAttributes"
        locale="tr"
        expanded
        borderless
        class="card"
      ></calendar>
    </div>
  </div>
</template>

<script>
import WorkspaceCollection from "@/collections/WorkspaceCollection";
import WorkspaceItem from "@/components/WorkspaceItem.vue";
import { Calendar } from "v-calendar";
import SurveyCollection from "@/collections/SurveyCollection";

export default {
  name: "AppDashboardPage",
  components: { WorkspaceItem, Calendar },
  data() {
    return {
      workspaces: new WorkspaceCollection(),
      surveys: new SurveyCollection(),
    };
  },
  created() {
    this.workspaces.fetchWithFilters();
    this.surveys.fetchWithFilters();
  },
  computed: {
    surveyAttributes() {
      return this.surveys.get().map((survey) => {
        return {
          key: survey.id,
          highlight: survey.survey_at.isBefore() ? "red" : "blue",
          dates: survey.survey_at.toDate(),
          popover: {
            label: survey.name,
          },
        };
      });
    },
  },
};
</script>

<style>
.vc-day {
  min-height: 52px;
}
.vc-highlight {
  width: 36px;
  height: 36px;
}
.vc-day-content {
  width: 36px;
  height: 36px;
  line-height: 36px;
}
.vc-monthly .is-not-in-month * {
  opacity: 0.5;
}
.vc-weekday {
  padding-top: 10px;
  padding-bottom: 14px;
}
</style>
